body {
  box-sizing: border-box;
  margin: 30px auto 0;
  padding: 0;
  font-family: Arial, sans-serif;
}

.people {
  flex-wrap: wrap;
  justify-content: flex-start;
  max-width: 1000px;
  margin: 0 auto;
  display: flex;
}

.person {
  flex-direction: column;
  align-items: center;
  margin-bottom: 40px;
  margin-right: 30px;
  display: flex;
  position: relative;
}

.photo {
  width: 200px;
  height: 200px;
  position: relative;
}

img {
  border-radius: 50%;
  max-width: 100%;
}

.rank {
  color: #8b0000;
  background-color: #fff;
  border-radius: 50%;
  justify-content: center;
  align-items: center;
  width: 16px;
  height: 16px;
  padding: 10px;
  font-size: 16px;
  display: flex;
  position: absolute;
  top: 50%;
  left: 85%;
  transform: translate(-50%, -50%);
}

.details {
  text-align: center;
  transition: transform 1s;
  position: relative;
}

.name {
  color: #333;
  margin-top: 10px;
  font-size: 20px;
  font-weight: bold;
  text-decoration: none;
  transition: font-size 1s;
  position: relative;
}

.extra {
  opacity: 0;
  justify-content: space-between;
  margin-top: 5px;
  transition: opacity 1s;
  display: flex;
  position: absolute;
  bottom: -30px;
  left: 50%;
  transform: translateX(-50%);
}

.person:hover .extra {
  opacity: 1;
}

.sport, .pay {
  flex: 1;
  transition: font-size 1s;
}

.pay:before {
  content: ", $";
}

.person:hover {
  background-color: #eee;
}

.person:hover .name {
  color: #fc0;
  font-size: 24px;
}

.person:hover .rank {
  width: 19px;
  height: 19px;
  transition: transform 1s;
}

.person:hover img {
  border-radius: 60px;
  transition: transform 1s;
  transform: scale(1.1);
}
